\"未发货\"></el-option>\n          <el-option value=\"1\"\n                     label=\"已发货\"></el-option>\n          <el-option value=\"2\"\n                     label=\"已收货\"></el-option>\n          <el-option value=\"3\"\n                     label=\"待确认\"></el-option>\n        </el-select>\n\n      </div>\n      <el-table :data=\"list\">\n        <el-table-column label=\"供应商名称\"\n                         prop=\"sm_name\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"运营商/维修厂名称\"\n                         prop=\"company\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"商品名称\"\n                         prop=\"type_name\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"品牌\"\n                         prop=\"gifts_brand\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"数量\"\n                         prop=\"num\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"订单价格\"\n                         prop=\"price\"\n                         align=\"center\">\n\n        </el-table-column>\n        <el-table-column label=\"申请时间\"\n                         prop=\"create_time\"\n                         align=\"center\"></el-table-column>\n        <el-table-column label=\"操作\"\n                         align=\"center\">\n          <template slot-scope=\"scope\">\n            <el-button type=\"primary\"\n                       size=\"small\"\n                       @click='scope.row.status==0 ? remind(scope.row) : confirm(scope.row)'\n                       :disabled=\"!(scope.row.status==0 || scope.row.status==3)\">{{scope.row.status==0 ? '提醒发货' :scope.row.status==1 ? '已发货' : scope.row.status == 2 ? '已确认' : '确认' }}</el-button>\n            <el-button type=\"primary\"\n                       @click=\"detailVisible=true,details(scope.row)\"\n                       size=\"small\">详情</el-button>\n          </template>\n        </el-table-column>\n      </el-table>\n      <!-- 订单详情  -->\n      <el-dialog title='详情'\n                 :visible.sync=\"detailVisible\"\n                 center\n                 width=\"40%\">\n\n        <div class=\"details\">\n          <p>\n            <span>详细地址: </span>\n            <span>{{`${detailList.province}${detailList.city}${detailList.county}${detailList.address}`}}</span>\n          </p>\n          <p>\n            <span>品牌: </span>\n            <span>{{detailList.gifts_brand}}</span>\n          </p>\n          <p>\n            <span>礼品类型: </span>\n            <span>{{detailList.gifts_standard}}</span>\n          </p>\n          <p>\n            <span>详情: </span>\n            <span>{{detailList.detail}}</span>\n          </p>\n\n        </div>\n\n      </el-dialog>\n      <div class=\"page_center\"\n           v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport Viewer from '@/utils/Viewer'\nexport default {\n  data () {\n    return {\n      list: [],\n      seCurId: '',\n      token: window.sessionStorage.getItem('bbytoken'), //token令牌\n      authList: [],\n      page: 0,  //供应商列表当前页数\n      pageCount: 0, //供应商列表总页数\n      detailVisible: false,\n      status: '',\n      detailList: []\n    }\n  },\n\n  methods: {\n    paging (e) {  //获取供应商列表当前页\n      this.page = e\n      this.init()\n    },\n    search () {  //搜索\n      this.init()\n    },\n    async init () {\n      try {\n        const res = await this.$axios.post('admin/SmDeliver/getGiftsDeliverList', { token: this.token, page: this.page, status: this.status })\n        this.list = res.data.data.list || []\n        this.pageCount = res.data.data.rows || 0\n      } catch (err) {\n        throw (err)\n      }\n    },\n    async details (row) {  //订单详情\n      try {\n        const res = await this.$axios.post('admin/SmDeliver/getGiftsDelicerDetail', { token: this.token, id: row.id })\n        this.detailList = res.data.data\n      } catch (err) {\n        throw (err)\n      }\n    },\n    //提醒发货\n    async remind (row) {\n      try {\n        const res = await this.$axios.post('admin/SmDeliver/remindDeliver', { token: this.token, sm_id: row.id })\n        if (res.data.code == 1) {\n          this.$message({ message: res.data.msg, type: 'success' })\n          this.init()\n        } else {\n          this.$message.error(res.data.msg)\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    async confirm (row) {  //确认\n      try {\n        const res = await this.$axios.post('admin/SmDeliver/adminGifts', { token: this.token, id: row.id, sm_id: row.sm_id })\n        if (res.data.code == 1) {\n          this.$message({ message: res.data.msg, type: \"success\" })\n          this.init()\n        } else {\n          this.$message.error(res.data.msg)\n        }\n      } catch (err) {\n        throw (err)\n      }\n    },\n    // 权限列表,当前所在权限页  \n    erAuth () {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      }).then(res => {\n        if (res.data.code == 1) {\n          var arr = res.data.data || [];\n          this.authList = arr;\n          console.log(this.authList)\n        } else {\n          this.$alert(res.data.msg, '提示', {\n            type: 'error'\n          });\n        }\n      })\n        .catch(err => {\n          alert(err);\n        })\n    }\n  },\n  created () {\n    this.erAuth()\n  },\n  mounted () {\n\n    this.init() //初始化数据  \n\n  },\n}\n</script>\n<style scoped>\n.search {\n  width: 100%;\n  overflow: hidden;\n}\n.search {\n  padding: 0px 0px 20px 50px;\n}\n\n.table img {\n  width: 50px;\n  height: 50px;\n  vertical-align: middle;\n  margin: 10px 0;\n}\n.ellipsis {\n  width: 80px !important;\n  white-space: nowrap !important;\n  text-overflow: ellipsis !important;\n  overflow: hidden !important;\n}\n.head_pic {\n  text-align: center;\n}\n.head_pic > p {\n  padding: 5px 0;\n}\n.head_pic > img {\n  width: 60px;\n  height: 60px;\n  border-radius: 50%;\n}\n.details span {\n  display: inline-block;\n  width: 48%;\n  padding: 10px 0;\n}\n.details p > span:last-child {\n  text-align: right;\n  \n}\n</style>"],"sourceRoot":""}]);

